<template>
    <div id="app">
        <top-bar></top-bar>
        <nav-bar></nav-bar>
        <router-view></router-view>

        <footer-bar></footer-bar>
    </div>
</template>

<script>
    import topBar from './components/TopBar/topBar.vue';
    import home from './components/Home/home.vue';
    import navBar from './components/NavBar/navBar.vue';
    import footerBar from './components/FooterBar/footerBar.vue';
    import allProducts from './components/DisplayProducts/displayProducts.vue';
    import store from './store';
    export default {
        name: "app",
        components: {
            topBar,
            home,
            navBar, 
            footerBar,
            allProducts
        },
        data() {
            return {
                
            };
        },
        methods: {
            getLocalState() {
                store.commit('LOCAL_STATE', JSON.parse(localStorage.getItem('Store')));
            }
        },
        mounted() {
            this.getLocalState();
        }
    };
</script>

<style>
</style>


